<template>
  <div :class="$style.content">
    <span :class="$style.title">{{titleToShow}}</span>
    <div :class="[$style.row, $style.headerRow]">
      <div :class="$style.item">Cundo</div>
      <div :class="$style.item">Cunto</div>
      <div :class="$style.item">Dnde</div>
    </div>
    <div :class="$style.row" v-for="s in spendsToShow" :key="`${s.date}-${s.amount}-${s.category}`">
      <div :class="$style.item">{{ s.date }}</div>
      <div :class="$style.item">{{ s.amount | formatPrice }}</div>
      <div :class="$style.item">{{ s.category }}</div>
    </div>
    <div v-if="limited" :class="$style.link"><router-link to="/month">Ver todo</router-link></div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import { formatPrice } from '@/utils';

export default {
  name: 'SpendsList',
  props: {
    limited: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      total: 4,
    };
  },
  computed: {
    ...mapState(['spends']),
    titleToShow() {
      if (this.limited) {
        return `ltimos ${this.total} gastos`;
      }
      return 'Todos los gastos';
    },
    spendsToShow() {
      if (this.limited) {
        return [...this.spends].splice(0, this.total);;
      }
      return [...this.spends];
    },
  },
  filters: {
    formatPrice,
  },
};
</script>

