import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import moment from 'moment';

const colorScheme = [
  "#25CCF7","#FD7272","#54a0ff","#00d2d3",
  "#1abc9c","#2ecc71","#3498db","#9b59b6","#34495e",
  "#16a085","#27ae60","#2980b9","#8e44ad","#2c3e50",
  "#f1c40f","#e67e22","#e74c3c","#ecf0f1","#95a5a6",
  "#f39c12","#d35400","#c0392b","#bdc3c7","#7f8c8d",
  "#55efc4","#81ecec","#74b9ff","#a29bfe","#dfe6e9",
  "#00b894","#00cec9","#0984e3","#6c5ce7","#ffeaa7",
  "#fab1a0","#ff7675","#fd79a8","#fdcb6e","#e17055",
  "#d63031","#feca57","#5f27cd","#54a0ff","#01a3a4"
];

const sortingFn = (a,b) => moment(a.createdAt).diff(b.createdAt);

Vue.use(Vuex);
export default new Vuex.Store({
  state: {
    spends: [],
    investments: [],
    allCategories: [],
    loading: false,
  },
  getters: {
    monthlySpend: (state) => state.spends
      .filter(x => moment(x.date).isSame(moment(), 'M'))
      .reduce((prev, curr) => prev + curr.amount, 0),
    todaySpend: (state) => state.spends
      .filter(x => moment(x.date).isSame(moment(), 'd'))
      .reduce((prev, curr) => prev + curr.amount, 0),
    categories: (state) => [...state.allCategories, ...state.spends.map((x) => x.category)]
      .reduce((prev, curr) => [...prev, ...(prev.includes(curr) ? [] : [curr])], []),
    moreRecentInvestment: (state) => state.investments
      .filter(x => moment(x.date).isSame(moment(), 'd'))[0],
    dayComparisionData: (state) => {
      moment.locale('es');
      const daysToCheck = 5;
      const labels = [...Array(daysToCheck).keys()]
        .map(i => moment().subtract(i, 'days').from(moment()))
        .reverse();
      const data = [...Array(daysToCheck).keys()]
        .map(i => state.spends.reduce((prev, curr) => prev + (moment(curr.date).isSame(moment().subtract(i, 'days').startOf('day'), 'd') ? curr.amount : 0), 0))
        .reverse();
      const backgroundColor = [...Array(daysToCheck).keys()]
        .map(i => `rgba(29, 99, 204, 1)`);
      return {
          labels,
          datasets: [
            {
              label: 'Gasto',
              backgroundColor,
              data,
            },
          ]
        };
    },
    monthPie: (state) => {
      const processed = state.spends
        .filter(x => moment(x.date).isSame(moment(), 'M'))
        .reduce((prev, curr) => Object.assign(
          prev,
          (
            prev[curr.category] !== undefined
            ? { [curr.category]: curr.amount + prev[curr.category] }
            : { [curr.category]: curr.amount }),
          ),
          {});
      return {
        datasets: [{
          data: Object.values(processed),
          backgroundColor: colorScheme.slice(0, processed.length),
      }],
      // These labels appear in the legend and in the tooltips when hovering different arcs
      labels: Object.keys(processed),
      };
    },
  },
  mutations: {
    saveInitData: (state, { spends, investments }) => {
      state.spends = spends.sort(sortingFn);
      state.investments = investments.sort(sortingFn);
    },
    setLoading: (state, payload) => {
      state.loading = payload;
    },
  },
  actions: {
    setLoading: ({ commit }, visibility) => {
      commit('setLoading', visibility)
    },
    login: ({ commit },) => {
      return axios.get(
        'https://users.dcc.uchile.cl/~sblasco/wallet/beta.php')
        .then(({ data }) => {
          commit('saveInitData', data);
          return Promise.resolve();
        });
    },
    addSpend: ({ state, commit }, payload) => {
      return axios.post('https://users.dcc.uchile.cl/~sblasco/wallet/spends.php', payload)
        .then(({ data }) => {
          commit('saveInitData', data);
          return Promise.resolve();
        });
    },
  },
  modules: {
  },
});
